/* Reset some basic elements for consistency across browsers */
body, h1, p, ul {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 20px;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  margin-bottom: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.App-header p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.App-nav {
  list-style: none;
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.App-nav a {
  text-decoration: none;
  color: #61dafb;
  font-size: 1.1rem;
}

.App-nav a:hover {
  text-decoration: underline;
}

.App-footer {
  margin-top: auto;
  padding: 10px 0;
  width: 100%;
  background-color: #20232a;
  color: white;
}

.App-footer p {
  margin: 0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
